  	*/

@font-face {
  font-family: "Flaticon";
  src: url(/static/media/Flaticon.22fd6bc0.eot);
  src: url(/static/media/Flaticon.22fd6bc0.eot?#iefix) format("embedded-opentype"),
       url(/static/media/Flaticon.2f290b31.woff2) format("woff2"),
       url(/static/media/Flaticon.da5c874e.woff) format("woff"),
       url(/static/media/Flaticon.8099ef6f.ttf) format("truetype"),
       url(/static/media/Flaticon.c14c3c84.svg#Flaticon) format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: "Flaticon";
    src: url(/static/media/Flaticon.c14c3c84.svg#Flaticon) format("svg");
  }
}

[class^="flaticon-"]:before, [class*=" flaticon-"]:before,
[class^="flaticon-"]:after, [class*=" flaticon-"]:after {   
  font-family: Flaticon;
        font-size: 20px;
font-style: normal;
margin-left: 10px;
}

.flaticon-right:before { content: "\f100"; }
.flaticon-arrow:before { content: "\f101"; }
.flaticon-left-arrow-angle-big-gross-symbol:before { content: "\f102"; }
.flaticon-down-chevron:before { content: "\f103"; }
.flaticon-information:before { content: "\f104"; }
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #0e1136;
  font-weight: 400;
  font-family: 'Muli', sans-serif;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a{
  color: #333;
  text-decoration: none;
}
button:focus {
  outline:0;
}

/* ------------------------------------ */

.overalApp{
  min-height: 100vh;
}

.leftCol{
  background: #EEF2F9;
}

.leftCol, .rightCol{
  min-height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  padding: 50px 0;
}
.leftCol .logoGH{
  position: absolute;
  left: 22px;
  top: 22px;
  height: 50px;
  cursor: pointer;
}
.leftCol h3, .rightCol h3{
  font-size: 26px;
  font-weight: 800;
  text-align: center;
  margin-bottom: 30px;
}

.rightCol h3 span{  
  color: #6A73D0;
  font-weight: 800;
}

.categoryButtons, .rightContent{
  margin: 0 auto;
  text-align: center;
}

.nextBtn{
  background: #25AE88;
  color: #fff;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  cursor: pointer;
  position: absolute;
  right: 20px;
  bottom: 20px;
}
.nextBtn:hover{
  background: #118968;
  -webkit-transition: .1s ease-in;
  transition: .1s ease-in;
}

.backBtn{
  background: transparent;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  cursor: pointer;
  position: absolute;
  left: 20px;
  bottom: 20px;
}
.backBtn:hover{
  text-decoration: underline;
  -webkit-transition: .1s ease-in;
  transition: .1s ease-in;
}

.nextBtn *{
  vertical-align: middle;
  color: #ffffff;
}

.nextBtn i{
  font-weight: 700;
}

.exportBtn{
  background: #25AE88;
  color: #fff;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  display: none;
  margin: 40px auto 0;
}
.exportBtn:hover{
  background: #118968;
  -webkit-transition: .1s ease-in;
  transition: .1s ease-in;
}

.exportBtn *{
  vertical-align: middle;
  color: #ffffff;
}

.exportBtn i{
  font-weight: 700;
}

.backBtn i::before{
  font-size: 11px;
  padding-right: 10px;
  color: #0d1036;
}

.endStyleLeft{
  display: none;
}

.endStyleRight{
  background: #EEF2F9;
}

.categoryButtons button{
  width: 100%;
  padding: 20px;
  background: #fff;
  border-radius: 12px;
  border: none;
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 10px;
}

.categoryButtons button:hover{
  background: rgba(106, 115, 208, 0.15);
  color: #fff;
  -webkit-transition: .1s ease-in;
  transition: .1s ease-in;
}

.categoryButtons .categoryInfo{
  display: inline-block;
  position: relative;
}

.categoryButtons .categoryInfo .information{
  visibility: hidden;
  position: absolute;
  top: -50px;
  left: 60px;
  min-width: 400px;
  background-color: #fff;
  padding: 30px 40px;
  border-radius: 15px;
  z-index: 1;
  text-align: left;
  box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.05);
}

.categoryButtons .categoryInfo:hover .information{
  visibility: visible;
}

.categoryButtons .categoryInfo .information h4{
  font-size: 15px;
  font-weight: 900;
}
.categoryButtons .categoryInfo .information p{
  font-size: 12.5px;
  line-height: 22px;
}

.categoryButtons .categoryInfo .information h4.extra-margin{
  margin-top: 30px;
}

.categoryButtons .categoryInfo .rTriangle{
  width: 30px;
  height: 60px;
  border-right: solid 30px rgb(255, 255, 255);
  border-bottom: solid 30px transparent;
  border-top: solid 30px transparent;
  position: absolute;
  left: -20px;
  top: 36px;
}

.warning{
  position: absolute;
  color: red;
  font-size: 13px;
}
/* BEGIN - Component: PreviewOption */

.previewBtnRow{
  background: #EEF2F9;
  border-radius: 50px;
  width: -webkit-fit-content;
  width: fit-content;
  width: -moz-fit-content;
  margin: 0 auto;
  margin-bottom: 40px;
}

.previewBtnRow button{
  border: none;
  border-radius: 50px;
  padding: 15px 25px;
  cursor: pointer;
  background: transparent;
}

.previewBtnRow button:hover{
  background: rgba(255, 255, 255, 0.55);
  -webkit-transition: .1s ease-in;
  transition: .1s ease-in;
}

@media screen and (max-width: 1250px){
  .previewBtnRow{
    background: transparent;
  }
  .previewBtnRow button{
    display: block;
    margin: 0 auto;
    padding: 10px 15px;
  }
}

/* END - Component: PreviewOption */
.previewTemplate {
	margin: 0 auto;
	-webkit-transition: 0.5s cubic-bezier(0, .21, 0, .98);
	transition: 0.5s cubic-bezier(0, .21, 0, .98);
	box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.1);
	background: #fff;
}


/* Preview mogelijkheden */

.PortraitPrv {
	width: 338px;
	height: 600px;
	overflow: hidden;
}

.LandscapePrv {
	width: 600px;
	height: 344px;
	overflow: hidden;
}

.SquaredPrv {
	width: 500px;
	height: 500px;
	overflow: hidden;
}


/* Standaard element eigenschappen */

.innerElements {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	position: relative;
	text-align: center;
}

.logo,
.main-title,
.subtitle {
	background: #D6DCE6;
	-webkit-transition: 0.5s cubic-bezier(0, .21, 0, .98);
	transition: 0.5s cubic-bezier(0, .21, 0, .98);
	z-index: 2;
}

.productimage,
.productvideo {
	background: #E2E6ED;
	-webkit-transition: 0.5s cubic-bezier(0, .21, 0, .98);
	transition: 0.5s cubic-bezier(0, .21, 0, .98);
	z-index: 2;
}

.backgroundvideo,
.background-image {
	background: repeating-linear-gradient(45deg,#eef2fa,#eef2fa 20px,#ffffff 20px,#ffffff 40px);
	-webkit-transition: 0.5s cubic-bezier(0, .21, 0, .98);
	transition: 0.5s cubic-bezier(0, .21, 0, .98);
	height: 98.5%;
	width: 97%;
	position: absolute !important;
	z-index: 1;
}

.backgroundvideo span,
.background-image span{
	color: #cccdd1 !important;
    background: #ffffff80;
}

.pancake-element {
	background: #adb1b7;
	width: 110px;
	height: 110px;
	border-radius: 50%;
	position: absolute !important;
	z-index: 3;
}

.pancake-element span {
	font-size: 13px !important;
}

.Product .backgroundvideo span,
.Frame .backgroundvideo span,
.Product .background-image span,
.Frame .background-image span {
	position: absolute;
	left: -81px;
	font-size: 15px !important;
	-webkit-transform: rotate(90deg);
	        transform: rotate(90deg);
}

.Frame .backgroundvideo span,
.Frame .background-image span {
	bottom: 73px;
	left: -77px;
}

.Frame.LandscapePrv .backgroundvideo span,
.Frame.LandscapePrv .background-image span {
	left: auto;
	right: -80px;
	bottom: auto;
}

.LandscapePrv .backgroundvideo,
.LandscapePrv .background-image {
	height: 97%;
	width: 98%;
}

.Product.LandscapePrv .backgroundvideo,
.Product.LandscapePrv .background-image {
	-webkit-transform: translateX(-25.5%);
	        transform: translateX(-25.5%);
}

.Frame.LandscapePrv .backgroundvideo,
.Frame.LandscapePrv .background-image {
	-webkit-transform: translateX(-27.5%);
	        transform: translateX(-27.5%);
}

.SquaredPrv .backgroundvideo,
.SquaredPrv .background-image {
	height: 98%;
	width: 98%;
}

.Product .innerElements *,
.Message .innerElements *,
.Frame .innerElements * {
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	font-weight: 900;
	font-size: 18px;
	text-transform: uppercase;
	color: #fff;
	position: relative;
}

.selected { 
	position: absolute !important;
	width: 20px;
	height: 20px;
	padding: 0px !important;
	right: -9px;
}

@media screen and (max-width: 1460px) {
	.PortraitPrv {
		width: 225px;
		height: 400px;
	}
	.LandscapePrv {
		width: 400px;
		height: 225px;
	}
	.SquaredPrv {
		width: 333px;
		height: 333px;
	}
	.Product .innerElements *,
	.Message .innerElements *,
	.Frame .innerElements * {
		font-size: 14px;
	}
}

