}

.ElementSelection input{
    margin-right: 20px;
}

.ElementSelection p{
    font-size: 18px;
    width: 50%;
}

.ElementSelection p span{
    cursor: pointer;
}

.selectionWrap {
    display: flex;
    flex-wrap: wrap;
}
.AnimationSelection {
    width: 70%;
    margin: 0 auto;
    text-align: left;
}

.AnimationSelection h3{
    text-align: left !important;
}

.selectFormAnimation{
    width: 50%;
